  height: 300px;
  background: #000;
  overflow: hidden; }

.inner-wrapper {
  width: 500%;
  height: 100%;
  position: relative;
  left: -100%; }

.slide {
  width: calc(100%/5);
  height: 100%;
  background: lightblue;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 100px;
  font-weight: 700;
  float: left; }

.button {
  width: 30px;
  height: 30px;
  border-top: 2px solid black;
  position: absolute;
  cursor: pointer; }

.prev {
  border-left: 2px solid black;
  left: 100px;
  transform: rotate(-45deg); }

.next {
  border-right: 2px solid black;
  right: 100px;
  transform: rotate(45deg); }

h2 {
  color: #000;
  font-size: 26px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  margin: 30px 0 80px; }

h2 b {
  color: #ffc000; }

h2::after {
  content: "";
  width: 100px;
  position: absolute;
  margin: 0 auto;
  height: 4px;
  background: rgba(0, 0, 0, 0.2);
  left: 0;
  right: 0;
  bottom: -20px; }

.carousel {
  margin: 50px auto;
  padding: 0 70px; }

.carousel .item {
  min-height: 330px;
  text-align: center;
  overflow: hidden; }

.carousel .item .img-box {
  height: 160px;
  width: 100%;
  position: relative; }

.carousel .item img {
  max-width: 100%;
  max-height: 100%;
  display: inline-block;
  position: absolute;
  bottom: 0;
  margin: 0 auto;
  left: 0;
  right: 0; }

.carousel .item h4 {
  font-size: 18px;
  margin: 10px 0; }

.carousel .item .btn {
  color: #333;
  border-radius: 0;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: bold;
  background: none;
  border: 1px solid #ccc;
  padding: 5px 10px;
  margin-top: 5px;
