  background: #000;
  border-color: #000;
  box-shadow: none; }

.carousel .item .btn i {
  font-size: 14px;
  font-weight: bold;
  margin-left: 5px; }

.carousel .thumb-wrapper {
  text-align: center; }

.carousel .thumb-content {
  padding: 15px; }

.carousel .carousel-control {
  height: 100px;
  width: 40px;
  background: none;
  margin: auto 0;
  background: rgba(0, 0, 0, 0.2); }

.carousel .carousel-control i {
  font-size: 30px;
  position: absolute;
  top: 50%;
  display: inline-block;
  margin: -16px 0 0 0;
  z-index: 5;
  left: 0;
  right: 0;
  color: rgba(0, 0, 0, 0.8);
  text-shadow: none;
  font-weight: bold; }

.carousel .item-price {
  font-size: 13px;
  padding: 2px 0; }

.carousel .item-price strike {
  color: #999;
  margin-right: 5px; }

.carousel .item-price span {
  color: #86bd57;
  font-size: 110%; }

.carousel .carousel-control.left i {
  margin-left: -3px; }

.carousel .carousel-control.left i {
  margin-right: -3px; }

.carousel .carousel-indicators {
  bottom: -50px; }

.carousel-indicators li, .carousel-indicators li.active {
  width: 10px;
  height: 10px;
  margin: 4px;
  border-radius: 50%;
  border-color: transparent; }

.carousel-indicators li {
  background: rgba(0, 0, 0, 0.2); }

.carousel-indicators li.active {
  background: rgba(0, 0, 0, 0.6); }

.star-rating li {
  padding: 0; }

.star-rating i {
  font-size: 14px;
  color: #ffc000; }

